projectReleaseReport.preparedHeadersForPDF = (data = []) =>
  data.map(col => {
    return { key: col.dataIndex, value: HEADERS_NAME_FOR_EXPORT[col.dataIndex] };
  });

  projectReleaseReport.preparedDynamicHeadersForPDF = (data = []) =>
  data.map(col => {
    return { key: col.dataIndex, value: col.title.props.title };
  });

  projectReleaseReport.getDynamicHeadersForExcel = (columns = [], filterVisibleColumns, exportAllData) => {
  let header = {};
  columns.forEach(item => {
    if (exportAllData || (filterVisibleColumns && filterVisibleColumns[item.key])) {
      header[item.key] = item.title.toUpperCase();
    }
  });
  return header;
};

projectReleaseReport.preparedDynamicDataForExport = (
  data = [],
  filterVisibleColumns,
  forExcel = true,
  dynamicColumns,
) => {
  let _data = data.map(record => {
    let item = {};
    for (const key in dynamicColumns) {
      if (dynamicColumns[key] === "date") {
        item[key] = record[key] ? moment(record[key]).format(DATE_FORMAT_DD_MM_YYYY) : "-";
      } else {
        item[key] = record[key] || "-";
      }
    }
    if (filterVisibleColumns) {
      Object.keys(item).forEach(itemKey => {
        if (!filterVisibleColumns[itemKey]) {
          delete item[itemKey];
        }
      });
    }
    return item;
  });
  return _data;
};

projectReleaseReport.preparedDataForExport = (data = [], filterVisibleColumns, forExcel = true) => {
  let headers = {};
  let _data = data.map(item => {
    const _item = {
      employeeId: item?.employeeId || "-",
      hrmsId: item?.hrmsId || "-",
      employeeName: item?.employeeName || "-",
      employeeCountry: item.employeeCountry || "-",
      employeeLocation: item?.employeeLocation || "-",
      employeeDateOfJoining: item?.employeeDateOfJoining
        ? moment(item.employeeDateOfJoining).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      parentAccountName: item?.parentAccountName || "-",
      reservedForAccountName: item?.reservedForAccountName || "-",
      projectName: item?.projectName || "-",
      domainName: item?.domainName || "-",
      subDomainName: item?.subDomainName || "-",
      allocationStatus: item?.allocationStatus || "-",
      allocationStartDate: item?.allocationStartDate
        ? moment(item.allocationStartDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      allocationEndDate: item?.allocationEndDate ? moment(item.allocationEndDate).format(DATE_FORMAT_DD_MM_YYYY) : "-",
      allocationAging: item?.allocationAging || "-",
      employeeTotalExperience: item?.employeeTotalExperience || "-",
      employeeMobileNumber: item?.employeeMobileNumber || "-",
      employeeDesignation: item?.employeeDesignation || "-",
      employeeEmail: item?.employeeEmail || "-",
      functionalGroupName: item?.functionalGroupName || "-",
      subStatus: item?.subStatus || "_",
      subStatusAging: item?.subStatusAging || "-",
      subStatusStartDate: item?.subStatusStartDate
        ? moment(item.subStatusStartDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      subStatusEndDate: item?.subStatusEndDate ? moment(item.subStatusEndDate).format(DATE_FORMAT_DD_MM_YYYY) : "-",
      cclName: item?.cclName || "-",
      cclEmail: item?.cclEmail || "-",
      employeeRole: item?.employeeRole || "-",
      onBehalfOf: item?.onBehalfOf || "-",
      employeeStatus: item?.employeeStatus || "-",
      employeeInactiveStatusDate: item?.employeeInactiveStatusDate
        ? moment(item?.employeeInactiveStatusDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
    };
    if (filterVisibleColumns) {
      Object.keys(_item).forEach(itemKey => {
        if (!filterVisibleColumns[itemKey]) {
          delete _item[itemKey];
        }
      });
    }
    if (forExcel) headers = Utils.makeHeaderDataForExcel(_item);
    return _item;
  });
  if (forExcel) _data.unshift(headers);
  return _data;
};
export default projectReleaseReport;
